@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@font-face {
  font-family: '8-bit_arcade_inmedium';
  src: url(/da-snake-1/static/media/8-bit_arcade_in-webfont.e13e03d1.woff2) format('woff2'),
       url(/da-snake-1/static/media/8-bit_arcade_in-webfont.a331393a.woff) format('woff');
  font-weight: normal;
  font-style: normal;

}

@font-face {
  font-family: '8-bit_arcade_outmedium';
  src: url(/da-snake-1/static/media/8-bit_arcade_out-webfont.f6c9bfd3.woff2) format('woff2'),
       url(/da-snake-1/static/media/8-bit_arcade_out-webfont.315617a7.woff) format('woff');
  font-weight: normal;
  font-style: normal;

}
* {
  box-sizing: border-box;
}



body {
  
  background-color: #0f0e17;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.board-container {
  position: relative;
  width: 80%;
  max-width: 500px;
  margin: auto;
}

.board-container:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.board {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: repeat(16, 1fr);
  justify-content: center;
  grid-gap: 4px;
  gap: 4px;
}
.square--b {
  background-color: #09080e;
  
}
.square--s {
  background-color:  #50fa7b;
}
.square--f {
  background-color: #ffb86c;
}
.square--o {
  background-color: #ff5555;
}

.controls {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
  width: 90vw;
  max-width: 500px;
  height: 15vh;
  
  display: grid;
  grid-gap: 10px;
  grid-template-areas:
   'left top right'
   'left bottom right';
}
.controls__button {
  font-family: '8-bit_arcade_inmedium', monospace;
  font-size: 2rem;
  color: #eb5cad;
  background: #09080e;
  text-align: center;
  border: 5px outset #09080e;
}
#left {
  grid-area: left;
}
#top {
  grid-area: top;
}
#bottom {
  grid-area: bottom;
}
#right {
  grid-area: right;
}


.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.arcade-out{
  z-index: 1;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  font-family: '8-bit_arcade_outmedium', monospace;
  font-size: 90px;
  color: #50fa7b;
}
.arcade-in{
  z-index: 1;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  font-size: 90px;
  color: #eb5cad;
  font-family: '8-bit_arcade_inmedium', monospace;
}
